footer {
	background: #34393f;
	.wrapper {
		&:not(.copy) {
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			padding: 75px 10px 63px 10px;
			flex-wrap: wrap;
		}
		&.copy {
			padding: 30px 10px; 
		}
	}
	.interview {
		&_head {
			color: rgba(255,255,255,0.5);
			& > div {
				margin-bottom: 9px;
				color: #fff;
			}
		}
		&_item {
			color: #fff;			
		}
	}
}
.footer {
	&_block {
		&_head {
			font-size: 24px;
			color: #fff;
			font-weight: bold;
			text-transform: uppercase;
			padding-bottom: 40px;
		}
		&_content {
			&:not(.social) {
				a {
					font-size: 18px;
					color: #fff;
					text-decoration: none;
					display: block;
					position: relative;
					padding-left: 25px;
					&:before {
						content: '';
						display: block;
						position: absolute;
						left: 0;
						top: 50%;
						border: 4px solid rgba(255,255,255,0);
						width: 0;
						height: 0;
						border-left-color: rgba(255,255,255,1);
						margin-top: -4px;
					}
					& + a {
						margin-top: 30px;
					}
				}
			}
		}
		&_interview {
			min-width: 390px;
			@media (max-width:660px) {
				width: 100%;
				padding-top: 25px;
				min-width: 0px;
			}
		}
		&_social {
			@media (max-width:1000px) {
				width: 100%;
				padding-top: 15px;
			}
			@media (max-width:660px) {
				padding-top: 25px;
			}
		}
	}
}
.interview {
	&_head {
		margin-bottom: 30px;
	}
	&_item {
		padding-bottom: 9px;
		& > div {
			margin-bottom: 9px;
			font-size: 14px;
			font-weight: 400;
			&:first-child {
				display: flex;
				justify-content: space-between;
				span + span {
					opacity: 0.4;
					font-size: 13px;
				}
			}
		}
		&_pr {
			padding: 3px;
			border-radius: 3px;
			background: #2a2e32;
			div {
				background-image: -webkit-linear-gradient(-50deg,rgba(255,255,255,.15) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,transparent 75%,transparent);
				background-image: -o-linear-gradient(-50deg,rgba(255,255,255,.15) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,transparent 75%,transparent);
				background-image: linear-gradient(-50deg,rgba(255,255,255,.15) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.15) 50%,rgba(255,255,255,.15) 75%,transparent 75%,transparent);
				-webkit-background-size: 40px 40px;
				background-size: 40px 40px;
				height: 14px;
				border-radius: 3px;
				&.green {
					background-color: #92d2ca;
				}
				&.orange {
					background-color: #ff7b4e;
				}
				&.blue {
					background-color: #75c8e3;
				}
			}
		}
	}
}
.copy {
	&:not(.wrapper) {
		border-top: 1px solid #494d53;
		color: rgba(255,255,255,.3);
		font-size: 14px;
	}
}