    url('../fonts/icomoon/icomoon.ttf?ylucl8') format('truetype'),
    url('../fonts/icomoon/icomoon.woff?ylucl8') format('woff'),
    url('../fonts/icomoon/icomoon.svg?ylucl8#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
 
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


