.aside {
	&_block {
		border-radius: 4px;
		border: 1px solid #ececec;
		padding: 30px 25px;
		margin-bottom: 20px;
		background-position: right bottom;
		background-repeat: no-repeat;
		&_head {
			font-size: 24px;
			text-transform: uppercase;
			color: #292929;
			font-weight: bold;
			letter-spacing: -1px;
			margin-bottom: 20px;
		}
	}
	& + .content {
		.topics {
			&_item {
				width: 24%;
				@media (max-width:875px) {
					width: 48%;
//					display: block;
//					border: 1px solid #ececec;
				}
				@media (max-width:490px) {
					width: 100%;
				}
				&_img {
//					@media (max-width:875px) {
//						max-width: 280px;
//						min-width: 280px;
//						float: left;
//						border: none;
//					}
				}
				
			}
		}
	}
	.interview_item_pr {
		background: #ececec;
		& + div {
			text-align: right;
			opacity: 0.4;
		}
	}
	.topics {
		&_item {
			width: 100%;
			text-align: center;
			padding-bottom: 47px;
			border-radius: 4px;
			overflow: hidden;
			@media (max-width:900px) and (min-width:570px) {
				display: block;
				border: 1px solid #ececec;
				padding-bottom: 0;
			}
			@media (max-width:570px) {
				&.doctor {
					max-width: 280px;
					margin: 0 auto 35px auto;
				}
			}
			&_img {
				margin-bottom: 30px;
				max-width: 280px;
				@media (max-width:900px) and (min-width:570px) {
					float: left;
					margin-bottom: 0;
				}
			}
		}
	}
	.doctor {
		&_position {
			
			&_top {
				margin-bottom: 15px;
				font-family: $pfb;
				font-size: 24px;
				color: #292929;
				display: block;
				@media (max-width:900px) and (min-width:570px) {
					padding-top: 50px;
				}
			}
		}
	}
}
.popular {
	&_topics {
		background-color: #77c7bd;
		background-image: url(../images/bg/popular_topics.png);
		border-color: #77c7bd;
		color: #fff;
		.aside_block_head,
		a {
			color: #fff;
			display: block;
		}
		a {
			font-size: 16px;
			line-height: 22px;
			letter-spacing: -0.25px;
			margin-bottom: 10px;
			&:hover {
				color: #e1fffb;
				text-decoration: none;
			}
		}
		.topics_item_info {
			color: #3b7a72;
			i {
				color: #3b7a72;
			}
		}
		&_item {
			& + * {
				margin-top: 15px;
				padding-top: 15px;
				border-top: 1px solid rgba(255,255,255,0.3);
			}
		}
	}
}
.actuality {
	font-size: 18px;
	color: #292929;
	text-align: center;
	border-color: #ff7b4e;
	background-image: url(../images/bg/actuality.png);
	&_item {
		min-height: 145px;
		text-align: left;
		letter-spacing: -0.5px;
		@media (max-width:900px) {
			text-align: center;
			min-height: 50px;
			padding-bottom: 20px;
		}
	}
}
.tags {
	&_item {
		display: inline-block;
		font-size: 14px;
		color: #498af3;
		padding: 3px 10px;
		border: 1px solid #e6e6e6;
		border-radius: 25px 0 0 25px;
		margin-bottom: 5px;
		&:hover {
			text-decoration: none;
		}
	}
}
.interview {
	a {
		position: relative;
		padding-left: 15px;
		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 50%;
			border: 4px solid rgba(255, 255, 255, 0);
			width: 0;
			height: 0;
			border-left-color: #9ba9b5;
			margin-top: -4px;
		}
	}
}
.popular {
	&_goods {
		&_item {
			display: flex;
			& + & {
				margin-top: 19px;
				border-top: 1px solid #e6e6e6;
				padding-top: 19px;
			}
			span {
				font-size: 13px;
				color: #636363;
				display: block;
			}
		}
	}
}