.topics {
		display: flex;
		justify-content: space-between;
		align-items: stretch;
		flex-wrap: wrap;
		&_item {
			width: 31%;
			border: 1px solid #ececec;
			border-top: none;
			border-radius: 2px;
			display: flex;
			flex-direction: column;
			align-items: stretch;
			margin-bottom: 20px;
			@media (max-width:760px) {
				width: 48%;
			}
			@media (max-width:520px) {
				width: 100%;
			}
			
			&:hover {
				border-color: #fff;
				box-shadow: 0 6px 32px rgba(0,0,0,0.1);
				.topics_item_img {
					border-color: rgba(255,255,255,0);
    				
				}
			}
			img {
				width: 100%;
				display: block;
			}
			a {
				display: block;
				text-decoration: none;
			}
			&_img {
				overflow: hidden;
				margin: -1px -1px 0px -1px; 
				border: 1px solid #ececec;
    			border-bottom: none;
				& + div {
					padding: 30px 20px 22px 20px;
					height: 100%;
					flex: 1 1;
					display: flex;
					justify-content: space-between;
					align-items: stretch;
					flex-direction: column;
				}
			}
			&_catagory {
				color: #ff7b4e;
				padding-bottom: 10px;
				border-bottom: 1px solid #ececec;
				margin-bottom: 30px;
				&:before {
					content: '';
					display: inline-block;
					vertical-align: top;
					margin-top: 7px;
					width: 10px;
					height: 10px;
					margin-right: 7px;
					background: url(../images/category.png) no-repeat 0 0;
				}
			}
			&_name {
				color: #498af3;
				font-size: 18px;
				line-height: 26px;
				height: 100%;
				margin-bottom: 25px;
			}
			&_info {
				color: #636363;
				i {
					color: #9ba9b5;
					font-size: 16px;
					vertical-align: middle;
					margin-top: -2px;
					display: inline-block;
					margin-right: 11px;
				}
			}
		}
		&.cat {
			& > * {
				width: 48%; 
				@media (max-width:520px) {
					width: 100%;
				}
			}
		}
		&.goods {
			.topics_item {
				&_img {
				display: flex;
				justify-content: center;
				align-items: center;
				border-bottom: 1px solid #ececec;
				@media (max-width:875px) and (min-width:570px) {
					max-width: 280px;
					min-width: 280px;
					float: left;
					border: none;
				}
					img {
						width: auto;
					}
				}
				@media (max-width:875px) and (min-width:570px) {
					width: 100%!important;
					display: block;
					border-top: 1px solid #ececec;
					&:after {
						content: '';
						display: block;
						clear: both;
					}
				}
			}
			@media (max-width:875px) and (min-width:570px) {
				display: block;
			}
		}
	}
.doctor {
	&s {
		.topics {
			&_item {
				border-top: 1px solid #ececec;
				border-radius: 4px;
				padding-top: 29px;
				padding-bottom: 29px;
				text-align: center;
				img {
					width: auto;
					display: inline-block;
				}
				&_img {
					margin-bottom: 35px;
					border-top: none;
				}
			}	
		}
	}
	&_name {
		font-weight: bold;
		color: #292929;
		font-size: 18px;
		line-height: 26px;
		margin-bottom: 25px;
	}
	&_position {
		color: #636363;
		font-size: 14px;
		line-height: 18px;
		display: block;
	}
	&_rev {
		padding: 30px 50px 30px 80px;
		position: relative;
		border-top: 2px solid #84d1c6;
		border-bottom: 2px solid #84d1c6;
		color: #537570;
		font-size: 18px;
		&:before {
			content: '';
			display: block;
			position: absolute;
			width: 40px;
			height: 46px;
			background: url(../images/doc.png) no-repeat 0 0;
			left: 5px;
			top: 30px;
		}
		span {
			display: block;
			font-weight: bold;
			
			color: #292929;
		}
		& + div {
			border-top: none;
		}
		&_block {
			margin-bottom: 50px;
		}
	}
}
.content {
	&_head {
		display: block;
		color: #292929;
		font-weight: bold;
		font-size: 36px;
		line-height: 36px;
		padding-bottom: 30px;
		text-transform: uppercase;
		letter-spacing: -2px;
		> span {
			color: #ff7b4e;
		}
	}
	&_block {
		padding-bottom: 25px;
		border-bottom: 1px solid #ececec;
		margin-bottom: 45px;
	}
	&_page {
		margin-bottom: 30px;
	}
}
.page {
	&_head {
		background: #1b87e3;
		padding: 28px 0 80px 0;
		
		& + * {
			border-radius: 7px;
			margin-top: -80px;
		}
		h1 {
			color: #fff;
			font-family: $pfb;
			font-size: 46px;
			letter-spacing: -0.25px;
			@media (max-width:450px) {
				font-size: 32px;	
			}
		}
	}
	&_one {
		&_head {
			border: 1px solid #ececec;
			padding: 18px;
			border-radius: 3px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 30px;
			.social {
				margin-right: 19px;  
			}
		}
		&_content {
			& > * {
				margin-bottom: 30px;
			}
			p {
				margin-bottom: 36px;
			}
			blockquote {
				border-top: 2px solid #84d1c6;
				border-bottom: 2px solid #84d1c6;
				padding: 30px 0 30px 80px;
				margin-bottom: 30px;
				position: relative;
				&.warning {
					color: #ff7b4e;
					border-color: #ff7b4e;
					&:before {
						color: #ff7b4e;
					}
				}
				&:before {
					font-family: 'icomoon' !important;
					speak: none;
					font-style: normal;
					font-weight: normal;
					font-variant: normal;
					text-transform: none;
					line-height: 1;
					-webkit-font-smoothing: antialiased;
					-moz-osx-font-smoothing: grayscale;
					
					content: "\e90b";
					
					position: absolute;
					top: 30px;
					left: 0;
					font-size: 46px;
					color: #84d1c6;
				}
			}
			h3 {
				font-size: 20px;
			}
			> img {
				width: 100%;
				border-radius: 5px;
				display: block;
				margin-bottom: 30px;
			}
			& ul,
			& ol {
				margin-top: 0;
			}
		}
	}
}
.bread {
	color: #ffffff;
	margin-bottom: 25px;
	a {
		color: #ffffff;
		display: inline-block;
		margin-right: 1px;
	}
	span {
		opacity: 0.5;
	}
	i {
		transform: rotate(-90deg);
		display: inline-block;
		margin-top: 0px;
		vertical-align: middle;
		font-size: 19px;
	}
}
.error {
	background: #f2f2f2;
	border-radius: 0!important;
	padding: 120px 0;
	.search {
		width: 100%;
		position: relative;
		margin: 33px 0 55px 0px;
		input {
			&[type=text] {
				background: #fff;		
			}
		}
	}
	&_block {
		.left {
			float: left;
			width: 55%;
			text-align: center;
			padding-right: 15px;
		}
		.right {
			float: right;
			width: 45%;
		}
		&:after {
			content: '';
			display: block;
			clear: both;
			width: 1px;
		}
		img {
			width: 100%;
			max-width: 480px
		}
	}
}
.pager {
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-top: 2px solid #ececec;
	margin-top: 15px;
	@media (max-width:690px) {
		flex-wrap: wrap;	
	}
	& > div {
		display: flex;
		justify-content: space-between;
		align-items: center;
		@media (max-width:690px) {
			width: 100%;
			order: 1;
		}
	}
	a {
		min-width: 60px;
		text-align: center;
		padding-top: 29px;
		padding-bottom: 30px;
		border-top: 2px solid #ececec;
		margin-top: -2px;
		text-decoration: none;
		@media (max-width:420px) {
			min-width: 30px;
		}
		&.current,
		&:hover {
			border-top-color: #77c7bd;
			color: #000;
		}
		
	}
	& > a {
		&:last-child,
		&:first-child {
			font-weight: 400;
			@media (max-width:690px) {
				width: 50%;
				order: 2;
			}
			i {
				display: inline-block;
				font-size: 20px;
				margin-top: -1px;
				vertical-align: middle;
				color: #9ba9b5;
			}
		}
		&:first-child {
			i {
				transform: rotate(90deg); 
				margin-right: 3px;
			}
		}
		&:last-child {
			i {
				transform: rotate(-90deg); 
				margin-left: 3px;
			}
		}
	}
}
.date {
	color: #636363;
	i {
		color: #9ba9b5;
		font-size: 24px;
		vertical-align: middle;
		padding-right: 10px;
	}
	span {
		color: #292929;
		font-weight: 500;
	}
	+ div {
		display: flex;
		align-items: center;
	}
	
}
.tabs {
	&_top {
		margin-bottom: 30px;
		a {
			padding: 18px 40px;
			border: 1px solid #ececec;
			border-radius: 3px 3px 0 0;
			display: inline-block;
			margin-right: -5px;
			font-size: 18px;
			&:hover {
				text-decoration: none;
			}
			&.active {
				border-bottom-color: #fff;
				color: #292929;
			}
		}
	}
	&_item {
		display: none;
		&.active {
			display: block;
		}
	}
}
.t_contents {
	padding: 30px;
	background: #f9f9f9;
	margin-bottom: 30px;
	ol {
		counter-reset: item;
		padding-left: 0;
		margin-bottom: 0;
		ol {
			margin-left: 28px;
		}
	}
	li {
		display: block
	}
	li:before {
		content: counters(item, ".") " ";
		counter-increment: item
	}
}
div.warning {
	border: 1px solid #faebcc;
	padding: 30px;
	background: #fbf8e2;
	margin-bottom: 30px;
	color: #cf9e52;
	b {
		color: #292929;
	}
}
.ad_di {
	li {
		list-style: none;
		position: relative;
		&:before {
			font-family: 'icomoon' !important;
			speak: none;
			font-style: normal;
			font-weight: normal;
			font-variant: normal;
			text-transform: none;
			line-height: 1;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
			
			content: "\e90a";
			
			color: #4bbe6c;
			position: absolute;
			top: 5px;
			left: -25px;
			font-size: 16px;
		}
		&.no {
			&:before {
				content: "\e909";
				color: #f45d59;
			}
		}
	}
}
.components {
	color: #ff7b4e;
	span {
		color: #636363;
	}
	i {
		font-weight: normal;
		font-style: normal;
		display: inline-block;
		padding: 0px 5px;
		border-radius: 2px;
		border: 1px solid #d7e9c5;
		background: #e0f0d8;
	}
	li {
		margin-bottom: 4px;
	}
}
.sale {
	padding: 30px 50px 30px 80px;
    position: relative;
    border-top: 2px solid #84d1c6;
    border-bottom: 2px solid #84d1c6;
    color: #292929;
    font-size: 18px;
	margin-bottom: 30px;
	&:before {
		content: '';
		display: block;
		position: absolute;
		width: 45px;
		height: 46px;
		background: url(../images/sale.png) no-repeat 0 0;
		left: 5px;
		top: 30px;
	}
	a {
		color: #cd572f;
		text-decoration: underline;
		&:hover {
			text-decoration: none;
		}
	}
}
.lr_block {
	&:after {
		content: '';
		display: block;
		width: 1px;
		clear: both;
	}
	.left {
		float: left;
		min-width: 43%;
		img {
			width: 100%;
		}
	}
	&.no_slider {
		.right {
			text-align: left;
		}
	}
	.right {
		float: right;
		width: 50%;
		text-align: center;
		.btn {
			margin-bottom: 25px;
		}
	}
	h1 {
		text-align: center;
	}
}
.slider {
	width: 360px!important;
	margin: 0 auto;
	&_item {
		padding: 7px;
	}
	.owl-nav {
		button {
			&.owl-next, 
			&.owl-prev {
				position: absolute;
				left: -25px;
				height: 44px;
				width: 11px;
				top: 50%;
				margin-top: -22px;
				text-indent: -99999px;
				background: url(../images/slider_nav.png) no-repeat 0 0!important;
			}
			&.owl-next {
				left: auto;
				right: -25px;
				background-position: top right !important;
			}
		}
	}
}