header {
	background: #fff;
	box-shadow: 0 10px 24px rgba(0,0,0,0.1);
	position: relative;
	z-index: 1;
	& > .wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-top: 30px;
		padding-bottom: 30px;
		@media (max-width:800px) {
			&:first-child {
				flex-wrap: wrap;
			}
		}
	}
	
}
.header {
	&_page {
		
	}
	&_letters {
		background: #f6f6f6;
		padding: 0 5px!important;
		margin-bottom: 20px;
		flex-wrap: wrap;
		border-radius: 4px;
		@media (max-width:950px) {
			justify-content: flex-start!important;
		}
		a {
			padding: 7px 9px;
			border-radius: 4px;
			position: relative;
			&:hover {
				background: #1b87e3;
				color: #fff;
				text-decoration: none;
				&:before {
					content: '';
					display: block;
					width: 0;
					height: 0;
					border: 4px solid rgba(255,255,255,0);
					border-top-color: #fff;
					position: absolute;
					top: 0;
					left: 50%;
					margin-left: -4px;
				}
			}
		}
	}
	.nav_b > a i {
		border: 1px solid #9facb8;
		color: #9facb8;
		border-radius: 50%;
		width: 10px;
		height: 10px;
		font-size: 10px;
		&:hover {
			color: #fff;
			border-color: #fff;
		}
		&:before {
			margin-left: -1px;
		}
	}
}
.logo {
	@media (max-width:520px) {
		width: 100%;
		text-align: center;
		margin-bottom: 20px;
	}
}
.search {
	width: 100%;
	position: relative;
	margin: 0 60px;
	@media (max-width:800px) {
		width: 100%;
		order: 5;
		margin-top: 20px;
	}
	@media (max-width:700px) {
		margin: 20px 0 0 0;	
	}
	&:focus,
	&:hover {
		input {
			&[type=text] {
				border-color: #1b87e3;
				& + * {
					background: #1b87e3;
					color: #fff;
				}
			}
		}
	}
	input {
		&[type=text] {
			background: #f2f2f2;
			display: block;
			width: 100%;
			padding: 15px 23px;
			border-radius: 4px;
			color: #000;
			font-size: 16px;
			border: 2px solid #f2f2f2;
			@include plhl {
				color: #636363;
			}
		}
		&:focus {
			border-color: #1b87e3;
			& + * {
				background: #1b87e3;
				color: #fff;
			}
		}
	}
	button {
		width: 50px;
		height: 52px;
		position: absolute;
		display: flex;
		justify-content: center;
		align-items: center;
		font-size: 20px;
		background: none;
		border: none;
		top: 0;
		right: 0;
		color: #636363;
		border-radius: 0 4px 4px 0;
	}
}
.social {
	width: 200px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	@media (max-width:520px) {
		width: 100%;
		justify-content: center;
	}
	> a {
		text-decoration: none;
		color: #fff;
		font-size: 16px;
		background: #000;
		width: 32px;
		height: 32px;
		border-radius: 4px;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 2.5px;
		&.icon-vk {
			background: #4f7198;
		}
		&.icon-instagram {
			background: #9250a5;
		}
		&.icon-twitter {
			background: #1da2f2;
		}
		&.icon-odnoklassniki {
			background: #f58120;
		}
		&.icon-facebook {
			background: #3c5998;
		}
	}
	&_rev {
		&_item {
			margin-bottom: 37px;
			i {
				font-size: 18px;
				vertical-align: top;
				margin-top: 4px;
				margin-right: 5px;
				display: inline-block;
			}
			&.vk {
				a {
					color: #4f7198;
				}
				i {
					color: #4f7198;
				}
			}
			&.od {
				
				a {
					color: #f5892e;
				}
				i {
					color: #f5892e;
				}
			}
		}
		&_head {
			margin-bottom: 25px;
			font-weight: bold;
		}
		&_content {
			padding: 25px 30px;
			border: 1px solid #ececec;
			border-radius: 20px;
			position: relative;
			&:before,
			&:after {
				content: '';
				display: block;
				width: 0;
				height: 0;
				border: 10px solid rgba(0,0,0,0);
				border-bottom-color: #ececec;
				top: -20px;
				left: 35px;
				position: absolute;
			}
			&:after {
				top: -18px;
				border-bottom-color: #fff;
			}
		}
	}
}
nav {
	border-top: 1px solid #ececec;
	a {
		text-decoration: none;
		font-size: 16px;
		color: #292929;
		&:hover {
			text-decoration: none;
		}
	}
	.wrapper {
		display: flex;
		align-items: stretch;
		justify-content: flex-start;
		@media (max-width:750px) {
			display: none;
			padding-left: 0;
			padding-right: 0;
			background: #498af3;
		}
	}
}
.nav {
	font-weight: 600;
	&_b {
		position: relative;
		& > a {
			padding: 22px;
			border-right:1px solid #ececec;
			display: block;
			transition: $trans;
			@media (max-width:750px) {
				border: none;	
				border-top: 1px solid rgba(255,255,255,0);
				color: #fff;
			}
			&.open {
				background: #fff;
				color: #498af3;
				border-color: #498af3;
				& + div {
					border-bottom: 1px solid #fff;
				}
				i {
					color: #498af3;
					transform: rotate(180deg);
				}
			}
			i {
				margin-left: 10px;
				vertical-align: middle;
				color: #93a2af;
				display: inline-block;
				transition: $trans;
			}
			
		}
		&:first-child {
			border-left:1px solid #ececec;
			@media (max-width:750px) {
				border: none;	
			}
		}
		& div {
			position: absolute;
			top: 100%;
			left: 0;
			white-space: nowrap;
			display: none;
			padding: 9px 0;
			a {
				display: block;
				padding: 11px 30px;
				font-size: 16px;
				color: #fff;
			}
			& > a {
				color: #fff;
				background: #498af3;
				i {
					transform: rotate(-180deg);
					color: #fff;
					margin-top: -2px;
				}
			}
			@media (max-width:750px) {
				position: relative;
				top: auto;
				left: auto;
				padding: 0;
			}
		}
		&:hover {
			& div {
				display: block;
				background: #498af3;
				
				left: 0;
				@media (max-width:750px) {
					display: none;
				}
			}
			
		}
	}
}