@import "variables";
@import "mixins";
html,
body {
	font-family: 'Roboto', sans-serif;
	background: #f2f2f2;
}
.wrapper {
	max-width: 1180px;
	margin: 0 auto;
	padding-left: 10px;
	padding-right: 10px;
}
.main {
	padding-top: 43px;
	padding-bottom: 60px;
	box-shadow: 0 4px 24px rgba(0,0,0,0.06);
	max-width: 1280px;
	margin: 0 auto 60px auto;
	background: #fff;
	border-radius: 0 0 5px 5px;
	color: #636363;
	font-size: 16px;
	line-height: 26px;
}
h1 {
	color: #292929;
	font-weight: bold;
	font-size: 36px;
	padding-bottom: 30px;
	text-transform: uppercase;
	letter-spacing: -2px;
	@media (max-width:450px) {
		font-size: 28px;
	}
}
h2 {
	color: #292929;
	font-size: 30px;
	text-transform: uppercase;
	margin-bottom: 30px;
	letter-spacing: -2px;
	@media (max-width:450px) {
		font-size: 24px;
	}
}
h3 {
	color: #292929;
	font-size: 24px;
	text-transform: uppercase;
	margin-bottom: 30px;
	letter-spacing: -0.25px;
	@media (max-width:450px) {
		font-size: 20px;	
	}
}
.main {
	padding-left: 10px;
    padding-right: 10px;
	.wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		flex-wrap: wrap;
	}
}
.content {
	width: calc( 100% - 300px );
	@media (max-width:900px) {
		width: 100%;
	}
}
aside {
	min-width: 280px;
	width: 280px;
	& + .content {
		width: 100%;
	}
	@media (max-width:900px) {
		width: 100%;
		min-width: 0px;
	}
}
b {
	font-weight: 500;
}
a {
	color: #498af3;
	font-weight: 500;
	text-decoration: none;
	&:hover {
		text-decoration: underline;
	}
	&.orange {
		color: #ff7b4e;
	}
}
.a {
	&_black {
		color: #292929;
	}
	
}
.btn {
	color: #fff;
	display: inline-block;
	background: #ff7b4e;
	border-radius: 70px;
	padding: 10px 30px;
	font-size: 14px;
	&:hover {
		text-decoration: none;
	}
	i {
		margin-right: 12px;
	}
}
.mob_btn {
	width: 100%;
	height: 63px;
	font-size: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	display: none;
	@media (max-width: 750px) {
		display: flex;
		cursor: pointer;
	}
	span {
		display: inline-block;
		vertical-align: middle;
		padding-right: 10px;
	}
		i {
			display: block;
			height: 3px;
			width: 35px;
			background: #1b87e3;
			transition: $trans;
			& + i {
				margin-top: 5px;
			}
			
		}
	&.open {
		i {
			&:first-child {
				transform: rotate(45deg);
    			margin-bottom: -16px;
				& + i {
					opacity: 0;
				}
			}
			&:last-child {
				transform: rotate(-45deg);
			}
		}
	}
}
table {
	width: 100%;
	border: none;
	td {
		padding: 10px 5%;
	}
	tr {
		&:nth-child(2n) {
			background: #f9f9f9;	
		}
	}
}
thead {
	text-align: left;
	background: #f2f2f2;
	th {
		font-weight: normal;
		font-size: 14px;
		color: #636363;
		padding: 10px 5%;
	}
}
.center {
	text-align: center;
	.btn {
		margin-bottom: 60px;
	}
}